@import '../variables';

.projects{
    margin-top: 25px;

    @include breakpoint(mediumWidth){
      display: flex;
      margin-top: 75px;
    }

    @include breakpoint(desktopWidth){
      margin-top: 100px;
    }

    .back{
        margin-left: 25px;
        margin-bottom: 20px;
        margin-top: 10px;
        font-size: 1.25em;
    
        @include breakpoint(mediumWidth){
            position: fixed;
            margin-left: 50px;
            margin-top: -25px;
        }
    
        @include breakpoint(desktopWidth){
            margin-top: -50px;
        }
    }

    .back:hover{
        span{
            transform: translateX(-5px);
            transition: transform .1s;
        }
    }

    .project{
        div{
            font-size: 1.5em;
            margin-bottom: 20px;
        }
    
        p{
            margin-bottom: 10px;
        }
    
        a{
            display: block;
            font-size: 1.5em;
            margin-bottom: 10px;
        }
    }
}

