@import "../variables";

.home{
    margin-top: 25px;

    @include breakpoint(mediumWidth){
      display: flex;
      margin-top: 50px;
    }

    @include breakpoint(desktopWidth){
      margin-top: 75px;
    }
}

@include breakpoint(mediumWidth){
  .left, .right{
      z-index: 2;
  }
}

.left{
    margin: 10px 25px 20px 25px;

    @include breakpoint(mediumWidth){
      position: fixed;
      margin: 0 0 50px 50px;
      width: calc(50% - 100px);
      height: calc(100% - 50px);
    }

    @include breakpoint(desktopWidth){
      height: calc(100% - 75px);
    }
    
}

.left h1{
    margin-bottom: 5px;
    font-size: 2.5em;

    @include breakpoint(mediumWidth){
      font-size: 3em;
    }
}

.left .sub{
    display: block;
    font-size: 1.25em;

    @include breakpoint(mediumWidth){
      font-size: 1.5em;
    }
}

.left p{
    font-size: 1.5em;
    font-style: italic;

    @include breakpoint(mediumWidth){
      font-size: 2em;
    }
}

.text{
  height: 40%;
}

.right{
    margin-left: 25px;
    margin-right: 25px;
    
    @include breakpoint(mediumWidth){
      width: calc(50% - 75px);
      margin-left: 50%;
    }

    @include breakpoint(desktopWidth){
      margin-right: 50px;
      width: calc(50% - 50px);
    }
}

.right h3{
    margin-bottom: 20px;
    font-style: italic;
}

.right .selected{
    margin-bottom: 75px;
}

@include breakpoint(mediumWidth){
  .left #blur{
    filter: blur(2px);
  }

  .blur{
      animation: blur ease-in-out .25s forwards;
  }
}

@keyframes blur {
    0% {
        filter: blur(2px);
    }
    100% {
        filter: blur(0px);
    }
}