@import './variables';

.appContainer{
    position: relative
}

.home, .projects{
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.home-enter, .home-appear{
    .left{
        opacity: 0;
        @include breakpoint(mediumWidth){
            transform: translateX(-10px);
        }
    }
    
    .right{
        opacity: 0;
        @include breakpoint(mediumWidth){
            transform: translateX(-5px);
        }
    }
}

.home-enter-active, .home-appear-active {
    .left{
        opacity: 1;
        transform: translateX(0);
        transition: .25s ease-in-out;
        transition-property: opacity, transform;
        @include breakpoint(mediumWidth){
            transition-delay: .45s;
        }
    }

    .right{
        opacity: 1;
        transform: translateX(0);
        transition: .25s ease-in-out;
        transition-property: opacity, transform;
        @include breakpoint(mediumWidth){
            transition-delay: .35s;
        }
    }
}

.home-exit {
    .left{
        opacity: 1;
        transform: translateX(0);
    }

    .right{
        opacity: 1;
        transform: translateX(0);
    }
}

.home-exit-active {
    .left{
        opacity: 0;
        transition: .25s;
        transition-property: opacity, transform;
        @include breakpoint(mediumWidth){
            transform: translateX(-5px);
        }
    }
    
    .right{
        opacity: 0;
        transition: .25s;
        transition-property: opacity, transform;
        @include breakpoint(mediumWidth){
            transform: translateX(-10px);
            transition-delay: .1s;
        }
    }
}

.projects-enter, .projects-appear{
    .left, .back{
        opacity: 0;
        @include breakpoint(mediumWidth){
            transform: translateX(10px);
        }
    }
    
    .right{
        opacity: 0;
        @include breakpoint(mediumWidth){
            transform: translateX(5px);
        }
    }
}

.projects-enter-active, .projects-appear-active {
    .left, .back{
        opacity: 1;
        transform: translateX(0);
        transition: .25s ease-in-out;
        transition-property: opacity, transform;
        @include breakpoint(mediumWidth){
            transition-delay: .45s;
        }
    }

    .right{
        opacity: 1;
        transform: translateX(0);
        transition: .25s ease-in-out;
        transition-property: opacity, transform;
        @include breakpoint(mediumWidth){
            transition-delay: .35s;
        }
    }
}

.projects-exit {
    .left, .back{
        opacity: 1;
        transform: translateX(0);
    }

    .right{
        opacity: 1;
        transform: translateX(0);
    }
}

.projects-exit-active {
    .left, .back{
        opacity: 0;
        transition: .25s;
        transition-property: opacity, transform;
        @include breakpoint(mediumWidth){
            transform: translateX(5px);
        }
    }
    
    .right{
        opacity: 0;
        transition: .25s;
        transition-property: opacity, transform;
        @include breakpoint(mediumWidth){
            transform: translateX(10px);
            transition-delay: .1s;
        }
    }
}